@import url('https://fonts.googleapis.com/css?family=Audiowide|Sofia|Trirong');

body{
    background-color: rgb(243, 243, 243);
    overflow-x: hidden;
    font-family: 'Courier New', Courier, monospace;
}

a{
    text-decoration: none;
}

input{
    display: none;
}

.container{
    width: 100%;
    height: 100%;
    max-width: 600px;
    max-height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;
    flex-direction: column;
    margin-bottom: 100px;
    margin: 0px auto;
}

.cards{
    position: relative;
    height: 500px;
    width: 430px;
    margin-bottom: 20px;
    perspective: 1000px;
    transform-style: preserve-3d;
}

.cards label{
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 120px;
    margin: auto;
    cursor: pointer;
    transition: 0.55s ease;
}

.cards .card{
    position: relative;
    height: 100%;
    background-color: rgb(215, 216, 216);
    border-radius: 10px;
    padding: 30px 35px;
}

.cards .image{
    display: flex;
    justify-content: space-between;
}

.cards .image img{
    border-radius: 5px;
    box-shadow: 15px 15px 40px rgba(0, 0, 0, 50%);
    margin-top: 23px;
    width: 86%;
    height: 220px;
    object-fit: cover;
    transition: all .7s;
}

.cards .image img:hover{
    transform: scale(1.05);
}

.cards .image .dots{
    text-align: center;
}

.dots div{
    width: 5px;
    height: 5px;
    border-radius: 60%;
    margin-bottom: 5px;
}

.dots div:nth-child(1){
    background-color: black;
}

.dots div:nth-child(2){
    background-color: rgb(0, 0, 0);
}

.dots div:nth-child(3){
    background-color: rgb(0, 0, 0);
}

.card .infos{
    display: block;
    text-align: center;
    padding-top: 60px;
}

.card .infos span{
    display: block;
}

.infos .name{
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    color: #17cf97;
    letter-spacing: 8px;
    margin-bottom: 20px;
    transition: all .6s ease;
}

.card .Lorem{
    font-size: 15.5px;
    color: rgb(5, 10, 9);
    letter-spacing: 3px;
}
@media screen and (max-width:840px){
    .cards{
        width: 400px;
    }
}
@media screen and (max-width:730px){
    .cards{
        width: 350px;
    }
}
@media screen and (max-width:670px){
    .cards{
        width: 320px;
    }
    .cards .image img{
        height: 180px;
    }
}
@media screen and (max-width:635px){
    .cards{
        width:250px;
        height: 400px;
    }
    .infos .name{
        font-size: 15px;
    }
}
@media screen and (max-width:490px){
    .cards{
        width:200px;
        height: 350px;
    }
    .infos .name{
        font-size: 15px;
    }
}

