nav{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(215, 216, 216);
    min-width: 100vw;
}
.navContainer{
    display: flex;
    align-items: center;
    width: 90vw;
    justify-content: space-between;
}
.Logo h2{
    font-size: 150%;
    color: #17cf97;
    font-weight: 1000;
}

.Logo h2 span{
    font-size: 180%;
}

#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a{
    text-decoration: none;
    font-size: 1rem;
    font-weight: 600;
    color: #000000;
    transition: 0.3s ease-in-out;
}

#navbar li a:hover, #navbar li a.active{
    color: #17cf97;
}

#navbar li a:hover::after, #navbar li a.active::after{
    content: "";
    width: 30%;
    height: 2px;
    background-color: #17cf97;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile{
    display: none;
}

#mobile i{
    color: #fff;
    align-items: center;
}
@media screen and (max-width:650px){
    #navbar{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 90px;
        right: -300px;
        width:300px;
        height: 100vh;
        background-color: rgb(215,215,216);
        box-shadow: o 40px 60px rgba(210, 49, 49, 0.01);
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;
        z-index: 100;
    }

    #navbar.active{
        right: 0px;
    }

    #navbar li{
        margin-bottom: 25px;
    }

    #mobile{
        display: block;
    }

    #mobile i{
        font-size: 24px;
        cursor: pointer;
    }
}


#bar:hover, #bar.active{
    color: #17cf97;
}
